import React, { useState } from 'react'
import clsx from 'clsx'

import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'

import Icon from '../../components/Icon'

//import i18n from '../../controller/i18n'
import Events from '../../controller/Events'
import Settings from '../../controller/Settings'



/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	QuickJump: {
		position: 'fixed',
		bottom: 0,
		width: '100%',
		backgroundColor: theme.palette.background.main,
		textAlign: 'center',
		borderTop: '2px solid #' + theme.palette.background.paper,
		zIndex: 1000,
	},
	QuickJumpOpener: {
		cursor: 'pointer',
		
		'&:hover': {
			backgroundColor: theme.palette.background.paper
		}
	},
	QuickJumpBody: {
		maxHeight: 500,
		padding: '5px 0 10px 0',
		overflow: 'auto'
	},
	JumpItemList: {
		width: '100%'
	},
	JumpItem: {
		color: theme.palette.text.primary
	},
	JumpItemHighlighted: {
		backgroundColor: theme.palette.primary.main,
		color: theme.palette.text.primary
	},
	JumpItemIconHighlighted: {
		color: theme.palette.text.primary
	}
}));


export default function QuickJump(props) {
	const classes = useStyles();
	
	const [open,setOpen] = useState(false);
	
	const events = new Events();
	const onJump = (drawer, tab, widget) => {
		setOpen(false);
		events.emit('tabs.navigate', tab, widget);
	}
	
	const openQuickJump = () => {
		setOpen(prevOpen => prevOpen !== true);
	}
	
	// hide QuickJumper
	const hideQuickJumper = Settings.getSetting('hideQuickJumper');
	if (hideQuickJumper === true) {
		return null;
	}
	
	// get layout
	const layout = Settings.getLayout();
	let QuickJumpComponents = [];
	
	// drawer
	for (let drawerIndex in layout) {
		let drawer = layout[drawerIndex];
		
		// tabs
		for (let tabIndex in drawer.tabs) {
			let tab = drawer.tabs[tabIndex];
			QuickJumpComponents.push(<JumpItem key={'JumpList-' + drawerIndex + '-' + tabIndex} highlighted={true} button={false} icon={tab.icon || ''} title={tab.title || ''} />);
			
			// fullscreen
			if (tab._fullscreen) {
				QuickJumpComponents.push(<JumpItem key={'JumpList-' + drawerIndex + '-' + tabIndex + '-fullscreen'} onJump={() => onJump({ ...drawer, index: drawerIndex }, { ...tab, index: tabIndex })} icon={tab.icon || 'home'} title={tab.title || ''} />);
			}
			
			// columns
			else {
				for (let columnIndex in tab.columns) {
					let column = tab.columns[columnIndex];
					
					// widgets
					for (let widgetIndex in column) {
						let widget = column[widgetIndex];
						if (widget) {
							QuickJumpComponents.push(<JumpItem key={'JumpList-' + drawerIndex + '-' + tabIndex + '-' + columnIndex + '-' + widgetIndex} onJump={() => onJump({ ...drawer, 'index': drawerIndex }, { ...tab, 'index': tabIndex }, { ...widget, 'index': widgetIndex, 'columnIndex': columnIndex })} icon={widget.icon || 'home'} title={widget.title || ''} />);
						}
					}
				}
			}
		}
	}
	
	return (
		<div className={clsx('jarvis-QuickJump', classes.QuickJump)}>
			<div className={classes.QuickJumpOpener} onClick={openQuickJump}>
				<Icon icon={'mdi-chevron-' + (open ? 'down' : 'up')} />
			</div>
			
			{open && <div className={classes.QuickJumpBody}>
				<List classes={{ root: classes.JumpList }}>
					{QuickJumpComponents}
				</List>
			</div>}
		</div>
	);
}

function JumpItem(props) {
	const classes = useStyles();
	
	return (
		<ListItem
			classes={{ root: clsx(classes.JumpItem, props.highlighted && classes.JumpItemHighlighted) }}
			button={props.button !== undefined ? props.button : true}
			onClick={props.onJump}
			>
			
			<ListItemIcon classes={{ root: clsx(classes.JumpItemIcon, props.highlighted && classes.JumpItemIconHighlighted) }}>
				<Icon icon={props.icon} />
			</ListItemIcon>
			<ListItemText primary={props.highlighted ? <strong>{props.title}</strong> : props.title} />
		</ListItem>
	);
}